/******************************/
/*old menu*/
div#header {
  width: 100%;
  direction: rtl;
  color: aqua;
  font-family: Arial, Helvetica, sans-serif;
  align-content: center;
  display: inline-table;
}

ul {
  /* height: 50px; */
  direction: rtl;
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #333;

  border-bottom-color: #38be33;
  border-bottom-style: solid;
  border-bottom-width: 3px;
}

li {
  /* height: 50px; */
  float: right;
  /* border-color: #444; */
  /* border-width: .1px; */
  /* border-style: solid; */
  font-size: 16px;
  text-align: center;
  justify-content: center;
  align-content: center;
}

li p {
  display: block;
  color: white;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
  cursor: pointer;
}

li p:hover {
  background-color: #333;
  color: lightgreen;
}

li p.active {
  background-color: #4caf50;
  color: white;
}

li p:hover:not(.active) {
  background-color: #555;
  color: white;
}
