}
div#serviceBox {
  direction: ltr;
  width: 120px;
  height: 90px;
  margin: 15px;
  margin-top: 7px;
  display: inline-table;
  border-width: 0px;
  border-color: #eee;
  border-style: solid;
  cursor: pointer;
  color: #000;
