div#slideContainer {
  width: 80%;
  height: 550px;
  margin: 2%;
  border-width: 1px;
  border-color: #eee;
  border-style: solid;
}
div#slideContainer img {
  width: 80%;
  height: 550px;
}
div.each-slide {
  height: 950px;
  border-width: 0px;
  border-color: rgb(214, 44, 44);
  border-style: solid;
}
div.each-slide img {
  width: 100%;
  height: 950px;
  margin-top: 0px;
}
div#infoBoxs {
  direction: rtl;
  width: 50%;
  height: auto;
  margin: 25%;
  margin-top: 0%;
  border-width: 1px;
  border-style: solid;
  display: inline-flex;
  text-align: center;
  align-content: center;
  align-items: center;
}
div#infoBox {
  width: 45%;
  height: 300px;
  margin: 10px;
  margin-top: 1%;
  border-width: 5px;
  border-color: #2222;
  border-style: solid;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
}
div#infoBox:hover {
  width: 45%;
  height: 300px;
  margin: 10px;
  margin-top: 1%;
  border-width: 5px;
  border-color: #2222;
  border-style: solid;
  margin-left: auto;
  margin-right: auto;
  cursor: pointer;
  background-color: #72c02c;
  color: #fff;
}
div#infoTitle {
  font-size: 20px;
}
div#infoText {
  font-size: 18px;
}
div#serviceCard {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  border-style: solid;
  border-width: 4px;
  border-color: #eda3;
  border-radius: 20px;
  background-color: #aaa2;
  cursor: pointer;
}
div#serviceCard:hover {
  width: 60%;
  margin-left: auto;
  margin-right: auto;
  margin-top: 30px;
  border-style: solid;
  border-width: 4px;
  border-color: #eda2;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  background-color: #2222;
  cursor: pointer;
}
